
<!--
组件向外提供接口事件有：
1、商品项被点击时产生的事件
-->

<template>
  <div >

      <div id="goodsType">

        <scrollView v-on:change-type="changeType($event)"/>

      </div>

      <div id="goodsList">
        <div v-for="(item, index) in list" :key="index" >
          <div  @click="$emit('add-goods',item)" >
          <Card  class="goodsItem" >
            <div class="itemTop" >
              <div style="width:20%;"><img src="../assets/logo.png"></div>
              <div style="color:red;">${{item.price}}</div>
            </div>
            <div class="itemBotton">
              <div>{{item.name}}</div>
              <div>({{item.id}})</div>
            </div>
          </Card>
          </div>
        </div>

      </div>

      <div id="pageBar">
        <Page :total="totalItem" :page-size=28  show-elevator v-on:on-change="changePage" />
        <!-- page-size=28 -->
      </div>

  </div>
</template>

<script>
import scrollView from './scrollView.vue'
import globalValue from '../utils/Global.vue'

export default {
    components:{
      scrollView
    },
    data(){
      return {
        list:[] ,
        typeIndex:0 ,
        totalItem: 0
      }
    },
    created: function(){
      
    },
    methods:{

      changeType(data){
        this.typeIndex = data ;
        var changeType = this.typeIndex ;

          this.$http.get('http://localhost:8080/commodity/listByType',
            {
              params: {
                'commodityType' : changeType,

                'page': 0 ,

                'size': 28
              }
            }).then(res=>{
              // 请求成功回调
              console.log(res)
              this.list = res.body.commodityList.content ;
              this.totalItem = res.body.commodityList.totalElements ;

            },resErr=>{

          });

        
        // this.$http({
        //     url: 'http://localhost:8080/commodity/listByType', //?commodityType=2&page=0&size=2
        //     method: 'GET',
        //     // header:{
        //     //     ContentType: "application/json charset=UTF-8"
        //     // },
        //     // 请求体重发送的数据
        //     params: {
              
        //       'commodityType' : changeType,

        //       'page': 0 ,

        //       'size': 28

        //     },
        // }).then(function (res) {
        //     console.log(res)
        //     this.list = res.body.commodityList.content ;
        //     this.totalItem = res.body.commodityList.totalElements ;
        //     // 请求成功回调
        // }, function () {
        //     // 请求失败回调
        // });

      },
      changePage(data){

        console.log(data)
        var changeType = this.typeIndex ;
        this.$http({
            url: 'http://localhost:8080/commodity/listByType', //?commodityType=2&page=0&size=2
            method: 'GET',
            // header:{
            //     ContentType: "application/json charset=UTF-8"
            // },
            // 请求体重发送的数据
            params: {
              
              'commodityType' : changeType,

              'page': data-1 ,

              'size': 28

            },
        }).then(function (res) {
            console.log(res)
            this.list = res.body.content ;
            // 请求成功回调
        }, function () {
            // 请求失败回调
        });

      }


    }
}
</script>




