<!--
组件向外提供接口的接口有：
1、类别项被点击时产生的事件
-->

<template>

        <div  id="wrapper" :style="style_val"  >
            <div style="display: flex; height:100%;">

                <div v-for="(item,index) in list" :key="index" @click="$emit('change-type',index)">
                    <div class="scroll-item" :style=" (clickedItemIndex == index) ? 'background-color:#97ACD4;':'' " 
                    @click='clickItem(index)'>[{{item.index}}] {{item.mean}}</div>

                </div>

            </div>
        </div>

</template>

<script>
	// FOOD(0,"粮油零食"),

	// DRINK(1,"酒水饮料"),

	// DAILY(2,"清洁洗护"),

	// ELETRICAL(3,"家电数码"),

	// COOKER(4,"厨房用品"),

	// BATH(5,"首饰美容"),

	// INFANT(6,"玩具母婴"),

	// FRESH(7,"果蔬生鲜"),

	// DRESS(8,"时装箱包"),

	// FURNITURE(9,"家具摆饰"),

	// SPORT(10,"体育棋牌"),

	// STUDY(11,"文具书簿"),

	// ELSE(12,"其他");
export default {

    name:"scrollView",
    
    data(){

        return {
            //视图风格数据
            style_val: "" ,
            direction: "x",
            x: 'scroll',
            y: 'hidden',
            height: "60px",
            width: "auto",
            
            lastClickedIndex:0,
            clickedItemIndex:0,

            //数据
            list: [{
                index:0,
                mean:"粮油零食"
            },{
                index:1,
                mean:"酒水饮料"
            },{
                index:2,
                mean:"清洁洗护"
            },{
                index:3,
                mean:"家电数码"
            },{
                index:4,
                mean:"厨房用品"
            },{
                index:5,
                mean:"首饰美容"
            },{
                index:6,
                mean:"玩具母婴"
            },{
                index:7,
                mean:"果蔬生鲜"
            },{
                index:8,
                mean:"时装箱包"
            },{
                index:9,
                mean:"家具摆饰"
            },{
                index:10,
                mean:"体育棋牌"
            },{
                index:11,
                mean:"文具书簿"
            },{
                index:12,
                mean:"其他"
            }]
        }

    },

    methods: {

        clickItem(data){

            this.lastClickedIndex = this.clickedItemIndex
            this.clickedItemIndex = data

            // if(this.lastClickedIndex == data)
            //     return this.clickedItemIndex = null ;

            //console.log(data)
        },

    },

    created(){

        //:{{y}}; ={{height}} width={{width}}',
        var val = 'overflow-x: ' + this.x  + '; '  ;

        val += 'overflow-y: ' + this.y  + '; '  ;

        val += 'height:' + this.height + '; '  ;

        val += 'width:' + this.width  + '; '  ;

        this.style_val = val ;

    }

}

</script>

<style>

#wrapper{
    display: flex;
    overflow: scroll;
}

.scroll-item{
    width: 100px;
    height: 100%;
    border:solid 1px #ccc ;
    border-width: 0px 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    cursor: pointer;
}

/* .scroll-item:hover{
    background-color:deepskyblue;
} */

</style>

