<!--
组件向外提供接口事件有：
1、结算按钮触发事件
2、数量+按钮触发事件
3、数量-按钮触发事件
4、删除按钮触发事件
5、清空按钮触发事件
6、记录按钮触发事件
7、会员按钮触发事件
-->

<template>
    <div>

            <div class="itemContainer" style="margin-top:10px;">
                <i-button @click="$emit('order-settle')" class="toolBtn" size="large" long type="primary" vertical="true">结算</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-add')" class="toolBtn" size="large" long type="warning" vertical="true">+</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-sub')" class="toolBtn" size="large" long type="error" vertical="true">-</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-del')" class="toolBtn" size="large" long vertical="true">删除</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('order-del')" class="toolBtn" size="large" long vertical="true">清空</i-button>
            </div>

            <div class="itemContainer">
                <i-button class="toolBtn" size="large" long vertical="true">记录</i-button>
            </div>

            <div class="itemContainer">
                <i-button class="toolBtn" size="large" long vertical="true">会员</i-button>
            </div>

            <div class="itemContainer" style="margin-top:200px;">
                <i-button class="toolBtn" size="large" long vertical="true">更多</i-button>
            </div> 

    </div>
</template>

<script>
export default {

    name:"orderToolbar"

}
</script>

<style scoped>

.itemContainer{
    width: 100%;
    padding: 5px 10px;

}

.toolBtn{
    display: block;
    font-size: 18px;
}
</style>
